@import './common';
@import './example';
@import './modal';

html{
    box-sizing: border-box;
}

*{
    box-sizing: inherit;
}

body{
    padding: 0px;
    margin: 0;
}

img {
    display: block;
    width: 100%;
    height: 170px;
  }

h1, h2, h3, h4, h5, h6{
    margin: 0;
    padding: 0;
}

ul{
    margin: 0;
    padding: 0;
    list-style: none;
}

a{
    text-decoration: none;
}

p{
    padding: 0;
    margin: 0;
}

// button{
